<script lang="ts">import { initReadonlyEditor } from "../codemirror/setup";
let value;
const [editor, useEditor] = initReadonlyEditor(value);
$:
  if (value !== editor.state.doc.toString()) {
    editor.dispatch({
      changes: { from: 0, to: editor.state.doc.length, insert: value }
    });
  }
export {
  value
};
</script>

<div use:useEditor />

<style>
  div {
    width: 100%;
    height: 100%;
  }
  div :global(.cm-editor) {
    width: 100%;
    height: 100%;
  }
</style>
