<script lang="ts">import { _ } from "../i18n";
import { isDescendant } from "../lib/account";
import { basename } from "../lib/paths";
import { sortFunc } from "../sort";
import { selectedAccount } from "./stores";
let data;
let selected = null;
function name(doc) {
  const base = basename(doc.filename);
  return `${doc.date}` === base.substring(0, 10) ? base.substring(11) : base;
}
const headers = [_("Date"), _("Name")];
const rowGetter = (d) => [d.date, name(d)];
let sortColumn = 0;
let sortOrder = "desc";
$:
  table = data.filter((doc) => isDescendant(doc.account, $selectedAccount)).map((doc) => ({ doc, row: rowGetter(doc) }));
$:
  sortedTable = table.sort(sortFunc("string", sortOrder, ({ row }) => row[sortColumn]));
function setSort(index) {
  if (index === sortColumn) {
    sortOrder = sortOrder === "asc" ? "desc" : "asc";
  } else {
    sortOrder = "asc";
    sortColumn = index;
  }
}
export {
  data,
  selected
};
</script>

<table>
  <thead>
    <tr>
      {#each headers as header, index}
        <th
          on:click={() => setSort(index)}
          data-sort
          data-order={index === sortColumn ? sortOrder : null}
        >
          {header}
        </th>
      {/each}
    </tr>
  </thead>
  <tbody>
    {#each sortedTable as { doc, row }}
      <tr
        class:selected={selected === doc}
        draggable={true}
        title={doc.filename}
        on:dragstart={(ev) => {
          ev.dataTransfer?.setData("fava/filename", doc.filename);
        }}
        on:click={() => {
          selected = doc;
        }}
      >
        <td>{row[0]}</td>
        <td>{row[1]}</td>
      </tr>
    {/each}
  </tbody>
</table>

<style>
  table {
    width: 100%;
  }
  tr {
    cursor: pointer;
  }
  .selected,
  tr:hover {
    background-color: var(--table-header-background);
  }
</style>
