<script lang="ts">import { select } from "d3-selection";
let axis;
let x = false;
let y = false;
let innerHeight = 0;
$:
  transform = x ? `translate(0,${innerHeight})` : void 0;
function use(node, ax) {
  const selection = select(node);
  ax(selection);
  return {
    update(a) {
      a(selection);
    }
  };
}
export {
  axis,
  innerHeight,
  x,
  y
};
</script>

<g class:y use:use={axis} {transform} />

<style>
  g :global(path),
  g :global(line) {
    fill: none;
    stroke: var(--chart-axis);
    shape-rendering: crispEdges;
  }
  g.y :global(line),
  g.y :global(path.domain) {
    opacity: 0.2;
  }
</style>
